import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			Q1: [{
					value: '我有出租经验',
					checked: false
				},
				{
					value: '这方面我是新手',
					checked: false
				}
			],
			Q2: [{
					value: '尽可能多点',
					checked: false
				},
				{
					value: '部分时间',
					checked: false
				},
				{
					value: '我还不确定',
					checked: false
				}
			],
			Q3: [{
					value: '即刻入住',
					checked: false
				},
				{
					value: '与我沟通联络',
					checked: false
				}
			],
			ownerName: '',
			ownerPhone: '',
			ownerQQ: '',
			ownerWechat: '',
			currentQ1asw: '', //当前Q1选择
			currentQ2asw: '', //当前Q2选择
			currentQ3asw: '' //当前Q3选择
		}
	},
	onLoad() {

	},
	onShow() {

	},
	methods: {
		save(){
			var data = {}
			data.currentQ1asw = this.currentQ1asw
			data.currentQ2asw = this.currentQ2asw
			data.currentQ3asw = this.currentQ3asw
			data.ownerName = this.ownerName
			data.ownerPhone = this.ownerPhone
			data.ownerQQ = this.ownerQQ
			data.ownerWechat = this.ownerWechat
			console.log(data)
			this.saveDataInfolist(data)
			uni.navigateBack()
		},
		...mapMutations(['saveDataInfolist']),
		inpownerName(e) {
			this.ownerName = e.detail.value
		},
		inpownerPhone(e) {
			this.ownerPhone = e.detail.value
		},
		inpownerQQ(e) {
			this.ownerQQ = e.detail.value
		},
		inpownerWechat(e) {
			this.ownerWechat = e.detail.value
		},
		checkboxChangeQ1(e) {
			var items = this.Q1,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			
			this.currentQ1asw = e.detail.value[0]
		},
		checkboxChangeQ2(e) {
			var items = this.Q2,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			this.currentQ2asw = e.detail.value[0]
		},
		checkboxChangeQ3(e) {
			var items = this.Q3,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			this.currentQ3asw = e.detail.value[0]
		}
	}
}
